.weather {
  width: 300px;
  border-radius: 6px;
  box-shadow: 10px -2px 20px 2px rgba(224, 22, 174, 0.3);
  color: #fff;
  background-color: #b52eeb;
  margin: 20px auto 0 auto;
  padding: 0 20px 20px 20px;
  mix-blend-mode:lighten;
}

.top,
.bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.city {
  font-weight: 600;
  font-size: 18px;
  line-height: 1;
  margin: 0;
  letter-spacing: 1px;
}
.weather-description {
  font-weight: 400;
  font-size: 14px;
  line-height: 1;
  margin: 0;
}
.weather-icon {
  width: 100px;
}
.temperature {
  font-weight: 600;
  font-size: 70px;
  width: auto;
  letter-spacing: -5px;
  margin: 10px 0;
}

.details {
  width: 150px;
  padding-left: 20px;
}
.parameter-row {
  display: flex;
  justify-content: space-between;
}
.parameter-label {
  text-align: left;
  font-weight: 400;
  font-style: 12px;
}
.parameter-value {
  text-align: right;
  font-weight: 600;
  font-style: 12px;
}
